@media (max-width: 767px) {
  .footer__nav-item--schedule {
    order: 6;
  }
}

.footer__nav-item a {
  color: #ffffff;
  font-family: "RobotoCondensed-Light", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  user-select: none;
}
@media (max-width: 767px) {
  .footer__nav-item a {
    font-size: 16px;
    line-height: 19px;
  }
}
@media (hover: hover) {
  .footer__nav-item a:hover:not(.focus-visible) {
    opacity: 0.7;
  }
}
.footer__nav-item a.focus-visible:focus {
  opacity: 0.7;
}

.footer__logo svg {
  margin-top: 2px;
}
@media (max-width: 1199px) {
  .footer__logo svg {
    width: 82px;
    margin-top: -8px;
  }
}
@media (max-width: 767px) {
  .footer__logo svg {
    width: 68px;
    margin-top: -11px;
  }
}
@media (hover: hover) {
  .footer__logo svg:hover:not(.focus-visible) {
    opacity: 0.7;
  }
}
.footer__logo svg.focus-visible:focus {
  opacity: 0.7;
}

.footer__social-list {
  grid-template-columns: auto auto auto;
  gap: 11px;
}
@media (max-width: 1199px) {
  .footer__social-list {
    margin-right: -6px;
  }
}
@media (max-width: 767px) {
  .footer__social-list {
    width: fit-content;
  }
}

.footer__social-list a:hover {
  opacity: 0.7;
}

.footer__social-link,
.footer__logo svg {
  fill: #ffffff;
}

